.alerts-panel[data-v-5975a999] {
	margin-bottom: var(--spacing-xl, 32px);
}

/*  Header  */
.alerts-panel__header[data-v-5975a999] {
	display: flex;
	align-items: flex-start;
	justify-content: space-between;
	margin-bottom: var(--spacing-lg, 24px);
	background: #fcfdff;
