	padding: var(--spacing-md, 16px) var(--spacing-lg, 24px);
}
.alerts-panel__title[data-v-5975a999] {
	font-size: 22px;
	font-weight: 600;
	color: var(--color-text-primary, #1a1a2e);
	margin: 0;
	padding: 0;
	border: none;
}
