}
.alerts-panel__details-grid[data-v-5975a999] {
		grid-template-columns: 1fr;
}
}
@media (max-width: 640px) {
.alerts-panel__summary-strip[data-v-5975a999] {
		grid-template-columns: repeat(2, 1fr);
}
}
